html,
body {
  width: 100%;
  height: 100%;
  background: #fff;
  overflow: hidden;
  padding: 0;
  margin: 0;
  font-family: "Roboto";
  overflow: auto;
}

* {
  box-sizing: border-box;
}

#root {
  width: 100%;
  min-width: 1100px;
  height: 100%;
  overflow: auto;
}

.App {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  font-family: sans-serif;
  text-align: center;
  overflow: auto;
}
